
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.marquee-example {
  width: 50%;
  margin: auto;
}

.wbpv-fire-text {
  font-family: impact;
  text-transform: uppercase;
  font-size: 4rem;
  position: relative;
  display: inline-block;
  margin: 0;
  padding-top: 0.66em;
  -webkit-text-stroke-width: 1px;
  z-index: 0;
}
.wbpv-fire-text:after {
  content: '';
  display: block;
  width: 100%;
  height: 100%;
  background: url(./static/img/fire.abf2621.gif) top;
  background-repeat: repeat-x; /* TODO: change to round */
  background-size: 2.25em;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.wbpv-fire-text.dark {
  color: black;
  -webkit-text-stroke-color: white;
}
.wbpv-fire-text.light {
  color: white;
  -webkit-text-stroke-color: black;
}

.marquee {
  overflow: hidden;
  position: relative;
}
.marquee p.scroll {
  position: absolute;
  width: 50%;
  height: 100%;
  margin: 0;
  line-height: 50px;
  text-align: center;
  /* Starting position */
  -webkit-transform: translateX(100%);
  transform: translateX(100%);
  /* Apply animation to this element */
}
/* Move it (define the animation) */
@-webkit-keyframes scroll {
0% {
    -webkit-transform: translateX(100%);
}
100% {
    -webkit-transform: translateX(-100%);
}
}
@keyframes scroll {
0% { /* Browser bug fix */
    -webkit-transform: translateX(140%); /* Browser bug fix */
    transform: translateX(140%);
}
100% { /* Browser bug fix */
    -webkit-transform: translateX(-140%); /* Browser bug fix */
    transform: translateX(-140%);
}
}
.marquee p.bounce {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
  line-height: 50px;
  text-align: center;
  /* Starting position */
  -webkit-transform: translateX(50%);
  transform: translateX(50%);
  /* Apply animation to this element */
}
/* Move it (define the animation) */
@-webkit-keyframes bounce {
0% {
    -webkit-transform: translateX(50%);
}
100% {
    -webkit-transform: translateX(-50%);
}
}
@keyframes bounce {
0% { /* Browser bug fix */
    -webkit-transform: translateX(50%); /* Browser bug fix */
    transform: translateX(50%);
}
100% { /* Browser bug fix */
    -webkit-transform: translateX(-50%); /* Browser bug fix */
    transform: translateX(-50%);
}
}

@-webkit-keyframes blink {
0% {
    visibility: hidden;
}
50% {
    visibility: hidden;
}
100% {
    visibility: visible;
}
}
@keyframes blink {
0% {
    visibility: hidden;
}
50% {
    visibility: hidden;
}
100% {
    visibility: visible;
}
}

.tile-background {
  width: 100%;
  min-height: 100%;
  padding: 1.5rem 0;
  background-image: url(./static/img/space.39adb7f.gif);
  background-repeat: repeat;
}

.body-text[data-v-ab9f847c] {
  font-size: 24px;
  font-weight: 700;
}

.wbpv-hr {
  border: 0;
  margin: 2rem 0;
  display: block;
  width: 100%;
  background-repeat: repeat-x;
  background-size: contain;
  background-position: center;
  height: 75px;
}
.wbpv-hr-rainbow {
  height: 7px;
  background: -webkit-gradient(linear, left top, right top, from(red), color-stop(orange), color-stop(yellow), color-stop(green), color-stop(blue), color-stop(indigo), to(violet));
  background: linear-gradient(to right, red, orange, yellow, green, blue, indigo, violet);
}
.wbpv-hr-butterfly {
  background-repeat: no-repeat;
  background-width: 400px;
  height: 120px;
}

.wbpv-frame-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.wbpv-frame {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
}
.wbpv-frame-left, .wbpv-frame-right {
  overflow-y: scroll;
  width: 30%;
  -o-border-image: linear-gradient(to left, #666, #ccc, #666) 0 100%;
     border-image: -webkit-gradient(linear, right top, left top, from(#666), color-stop(#ccc), to(#666)) 0 100%;
     border-image: linear-gradient(to left, #666, #ccc, #666) 0 100%;
}
.wbpv-frame-container-left {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.wbpv-frame-left {
  border-right: 5px solid;
}
.wbpv-frame-container-right {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: reverse;
      -ms-flex-direction: row-reverse;
          flex-direction: row-reverse;
}
.wbpv-frame-right {
  border-left: 5px solid;
  right: 0;
}


.wbpv-under-construction {
  display: block;
  margin: 2rem auto;
}
